import {myAPI, profileApi} from "../api/api";

const ADD_POST = 'ADD_POST';
const SET_USER_PROFILE = 'SET_USER_PROFILE';
const SET_USER_STATUS = 'SET_USER_STATUS';
const DELETE_POST = 'DELETE_POST';

let initialState = {
    postsData: [
        {id: 1, message: 'Hello', likesCount: 12},
        {id: 2, message: 'how are you', likesCount: 25},
        {id: 3, message: 'bitch', likesCount: 3}
    ],
    userProfile: null,
    userStatus: ''
};


const profileReducer = (state = initialState, action) => {
    switch (action.type) {
        case ADD_POST:
            let newPost = {
                id: 5,
                message: action.newPostText,
                likesCount: 0

            };
            return {
                ...state,
                postsData: [...state.postsData, newPost]
            };

        case SET_USER_PROFILE:
            return {
                ...state,
                userProfile: action.profile
            };

        case SET_USER_STATUS:
            return {
                ...state,
                userStatus: action.userStatus
            };
        case DELETE_POST:
            return {
                ...state,
                postsData: state.postsData.filter(p => p.id !== action.postId)
            };

        default:
            return state;
    }
}


export const addPostActionCreator = (text) => ({type: ADD_POST, newPostText: text});
export const setUserProfile = (profile) => ({type: SET_USER_PROFILE, profile: profile});
export const setUserStatus = (userStatus) => ({type: SET_USER_STATUS, userStatus: userStatus});
export const deletePost = (postId) => ({type: DELETE_POST, postId: postId});


export const getProfile = (userId) => {
    return async (dispatch) => {
        const response = await myAPI.getProfile(userId)
        dispatch(setUserProfile(response));
    }
}

export const getStatus = (userId) => {
    return async (dispatch) => {
        const response = await profileApi.getStatus(userId)
        dispatch(setUserStatus(response));
    }
}

export const updateStatus = (status) => {
    return async (dispatch) => {
        const response = await profileApi.updateStatus(status)
        if (response.resultCode === 0) {
            dispatch(setUserStatus(status));
        }
    }
}


export default profileReducer;