import loaderGif from "../../../assets/images/loader.gif";
import s from './Preloader.module.scss';
import React from "react";


let Preloader = (props) => {
    return (
        <div className={s.preloader_box}>
            <img
                src={loaderGif} className={s.preloader_img}
                alt='preloader'
            />
        </div>

    )
}

export default Preloader;